import React, { FC } from 'react';
import { fetchPlannerPartners } from 'src/api/Planner';
import Container from './Container';
import PartnerRow from './PartnerRow';
import { useQuery } from 'react-query';
import { EmptyText } from './components/styles';

interface PartnerInfoProps {
  plannerId: string;
}

export const PartnerInfo: FC<PartnerInfoProps> = ({ plannerId }) => {
  const { data: partnerInfo } = useQuery(['partners', plannerId], () => fetchPlannerPartners(plannerId));

  return (
    <Container title="  ">
      {
        partnerInfo && Object.keys(partnerInfo).length ?
          <>
            <PartnerRow title="" partner={partnerInfo.STUDIO} />
            <PartnerRow title="" partner={partnerInfo.DRESS} />
            <PartnerRow title="" partner={partnerInfo.MAKEUP} />
          </> :
          <EmptyText>    .</EmptyText>
      }
    </Container>
  );
};

